.header-pretitle {
    color: #95aac9;
    letter-spacing: .08em;
    text-transform: uppercase;
    font-size: 12px;
}
.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.header-title {
    margin-bottom: 0;
}
.border-top-2 {
    border-top-width: 2px !important;
  }
  .bg-auth {
    background-color: #fff !important;
  }
  .border-primary {
    border-color: #2c7be5 !important;
  }
  .border-top {
    border-top: 1px solid #e3ebf6 !important;
  }
  .display-4 {
    font-size: 2rem;
      font-weight: 600;
      line-height: 1.1;
  }
  .text-muted {
    --bs-text-opacity: 1;
    color: #95aac9 !important;
  }
  .form-group {
    margin-bottom: 14px;
  }
  .form-label {
    margin-bottom: 0.5rem;
    color: #12263f;
    font-weight: 400;
  }
  .form-control {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-clip: padding-box;
    background-color: #fff;
    border: 1px solid #d2ddec;
    border-radius: 0.375rem;
    color: #12263f;
    display: block;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.5;
    padding: 0.5rem 0.75rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    width: 100%;
  }
  .btn-primary {
    background-color: #2c7be5;
    border-color: #2c7be5;
    color: #fff;
  }
  @media (min-width: 576px) {
    .container,
    .container-sm {
      max-width: 540px;
    }
  }
  @media (min-width: 768px) {
    .container,
    .container-md,
    .container-sm {
      max-width: 720px;
    }
  }
  
  @media (min-width: 992px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm {
      max-width: 960px;
    }
  }
  @media (min-width: 1200px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl {
      max-width: 1140px;
    }
  }
  .mb-0 {
    margin-bottom: 0 !important;
  }
  .mb-1 {
    margin-bottom: 0.1875rem !important;
  }
  .mb-2 {
    margin-bottom: 0.375rem !important;
  }
  .mb-3 {
    margin-bottom: 0.75rem !important;
  }
  .mb-4 {
    margin-bottom: 1.5rem !important;
  }
  .mb-5 {
    margin-bottom: 2.25rem !important;
  }
  .mb-6 {
    margin-bottom: 4.5rem !important;
  }
  .mb-7 {
    margin-bottom: 6.75rem !important;
  }
  .mb-8 {
    margin-bottom: 13.5rem !important;
  }
  .mb-auto {
    margin-bottom: auto !important;
  }
  .btn-lg {
    border-radius: 0.5rem;
    font-size: 0.9375rem;
    padding: 0.75rem 1.25rem;
  }
  .form-control:focus {
    background-color: #fff;
    border-color: #2c7be5;
    box-shadow: none;
    color: #12263f;
    outline: 0;
  }
  .input-group-text {
      align-items: center;
      background-color: #fff;
      border: 1px solid #d2ddec;
      border-radius: 0.375rem;
      color: #95aac9;
      display: flex;
      font-size: .9375rem;
      font-weight: 400;
      line-height: 1.5;
      padding: 0.5rem 0.75rem;
      text-align: center;
      white-space: nowrap;
  }
  .btn {
    border: 1px solid transparent;
    border-radius: 0.375rem;
    display: inline-block;
    font-size: .9375rem;
    font-weight: 400;
    line-height: 1.5;
    padding: 0.5rem 0.75rem;
    text-align: center;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    vertical-align: middle;
}
.btn-primary {
  background-color: #2c7be5;
  border-color: #2c7be5;
  color: #fff;
}
.form-check-label {
  font-size: 15px;
  font-weight: 400;
  color: #000;
}